.card-cours {
    border: 1px solid #eee;
    padding: 20px;
    border-radius: 4px;
    height: 100%;

    &:hover {
        border: 1px solid $yellow;
    }

    @media screen and (min-width: $w1200) {
        display: flex;
        align-items: center;
    }

    &__image {
        max-width: 150px;
        margin: 0 auto;
        padding-bottom: 10px;

        @media screen and (min-width: $w1200) {
            padding-bottom: 0;
            margin-left: 0;
            margin-right: 30px;
        }
    }

    &__title {


        padding-bottom: 15px;

        & a {
            font-weight: 600;
            font-size: 18px;
            text-align: center;

            @media screen and (min-width: $w1200) {
                text-align: left;
                font-size: 22px;
            }
        }

    }

    &__link {
        color: #666;
        padding-bottom: 10px;
        text-align: center;

        @media screen and (min-width: $w1200) {
            text-align: left
        }
    }

    &__desc {
        font-size: 13px;
        color: #888;
        text-align: center;

        @media screen and (min-width: $w1200) {
            text-align: left
        }
    }

}

.courses-block {
    &__list {
        margin: -10px;
        display: flex;
        flex-wrap: wrap;
    }

    &__item {
        padding: 10px;
        width: 100%;

        @media screen and (min-width: $w992) {
            width: 50%;
        }

        @media screen and (min-width: $w1200) {
            width: 100%;
        }
    }
}