.lesson {
    &__details {
        padding-bottom: 30px;
    }

    &__task {
        padding: 25px;
        background-color: #eee;
        margin-bottom: 30px;
        border-left: 3px solid #f4b43f;
    }

    &__content {
        border-bottom: 1px solid #000;
        margin-bottom: 50px
    }

    &__title {
        @include section-title-h2;
        text-align: left;
        padding-bottom: 20px;
    }

    &__nav {

        @media screen and (min-width: $w760) {
            display: flex;
            justify-content: space-between;
        }

        margin-bottom: 30px;

        &-next {
            margin-bottom: 10px;
        }

        &-prev {
            margin-bottom: 10px;
        }

        & button {
            width: 100%;
        }
    }

}

.lesson-box {
    &__header {
        padding: 10px;
        border: 2px dashed #7f7f7f;
        text-align: center;
        font-weight: 600;
        font-size: 16px;

        margin-bottom: 30px;
    }

    &__video {
        margin-bottom: 30px;
    }

    &__text {
        & strong {
            font-weight: 600;
        }


    }

    &__audio {
        margin-bottom: 30px;
    }


    &__spoiler {

        margin-bottom: 30px;

        &-title {
            font-weight: 600;
        }

        &-text {
            max-height: 100px;
            overflow: hidden;
            position: relative;
            transition: 0.3s ease;

            &:not(.open) {
                &::before {
                    content: '';
                    display: block;
                    box-shadow: 0 0 14px 10px #fff;
                    position: absolute;
                    left: 0;
                    right: 0;
                    bottom: 0;
                }
            }

            &.open {
                max-height: 90vh;
                overflow-y: auto;
            }
        }

        &-btn {

            background-color: #eee;
            padding: 10px;
            margin-top: 20px;
            text-transform: uppercase;
            font-weight: 600;
            text-align: center;
            cursor: pointer;

        }
    }


}