.card-lesson {
    border: 1px solid #eee;
    overflow: hidden;
    border-radius: 4px;
    height: 100%;

    &:hover {
        border: 1px solid $yellow;
    }

    @media screen and (min-width: $w1200) {
        display: flex;
        align-items: center;
    }

    &__image {
        width: 100%;
        margin: 0 auto;
        padding-bottom: 10px;

        @media screen and (min-width: $w1200) {
            min-height: 100%;
            width: 155px;
        }

        & img {
            height: 100%;
        }

        & picture {
            display: block;
            padding-top: 67%;
            position: relative;

            & img {
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        @media screen and (min-width: $w1200) {
            padding-bottom: 0;
            margin-left: 0;
            margin-right: 30px;
        }
    }

    &__arrow {
        display: none;

        @media screen and (min-width: $w1200) {
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 0 10px;
            background-color: $red;

            & svg {
                fill: #fff;
            }
        }
    }

    &__title {




        & a {
            font-weight: 600;
            font-size: 18px;
            text-align: center;

            display: inline-block;



            @media screen and (min-width: $w1200) {
                text-align: left;
                font-size: 22px;
            }
        }

    }

    &__main {
        padding: 15px;

        @media screen and (min-width: $w1200) {
            display: flex;
            justify-content: center;
            flex-direction: column;
        }
    }

    &__link {
        color: #666;
        padding-bottom: 10px;
        text-align: center;

        @media screen and (min-width: $w1200) {
            text-align: left
        }
    }

    &__wrn {
        padding-bottom: 10px;
        font-size: 13px;
        color: $red;
        font-weight: 600;
    }

    &__desc {
        font-size: 13px;
        color: #888;
        text-align: center;

        @media screen and (min-width: $w1200) {
            text-align: left
        }
    }

}


.cours-detail {
    &__list {
        margin: -7px;
        display: flex;
        flex-wrap: wrap;
    }

    &__item {
        padding: 7px;
        width: 100%;

        flex-basis: 240px;
        flex-grow: 1;

        @media screen and (min-width: $w1200) {
            width: 100%;
            flex-basis: 100%;
        }
    }
}